<template>
    <div class="phi-page">
		<div class="phi-page-cover">
			<div class="phi-page-toolbar">
				<button class="phi-button" @click="$router.go(-1)"> <i class="fa fa-arrow-left"></i></button>
				<h1>{{ $route.params.nodeId }}</h1>
			</div>
		</div>

        <div class="phi-page-contents">
            <div class="phi-card">
                <div v-for="entry in log.items.reverse()" class="phi-media entry">
                    <div class="phi-media-figure phi-avatar">
                        <img :src="entry.person.avatar" alt="entry.person.firstName">
                    </div>
                    <div class="phi-media-body">
                        <h1 class="name">{{ entry.person.firstName }} {{ entry.person.lastName }}</h1>
                        <span class="type">{{ entry.type }}</span>
                        <span class="timestamp">{{ moment.unix(entry.timestamp).calendar(null, {sameElse: 'MMM D'}) }}</span>
                    </div>
                </div>

                <phi-drawer :open="tpl.drawerIsOpen">
                    <fieldset>
                        <select v-model="entry.type">
                            <option v-for="ntype in types.items" :value="ntype.singular">{{ ntype.singular }}</option>
                        </select>

                        <phi-person-picker :api="app.api" label="buscar" @select="selectPerson"></phi-person-picker>
                    </fieldset>

                    <footer>
                        <div class="phi-media save" @click="saveEntry">
                            <i class="phi-media-figure fa fa-check"></i>
                            <div class="phi-media-body">{{ $t('save') }}</div>
                        </div>

                        <div class="phi-media cancel" @click="tpl.drawerIsOpen = false">
                            <i class="phi-media-figure fa fa-times"></i>
                            <div class="phi-media-body">{{ $t('cancel') }}</div>
                        </div>
                    </footer>

                </phi-drawer>

                <phi-drawer :open="!tpl.drawerIsOpen">
                    <div class="phi-media" @click="tpl.drawerIsOpen = true">
                        <i class="phi-media-figure fa fa-plus"></i>
                        <div class="phi-media-body">{{ $t('log event') }}</div>
                    </div>
                </phi-drawer>

            </div>

        </div>
    </div>
</template>

<script>
import app from '../../store/app.js';
import moment from 'moment';

import PhiDrawer from '../../components/Phi/Drawer.vue';
import PhiInput from '../../components/Phi/Input.vue';
import PhiPersonPicker from '../../components/Phi/Person/Picker.vue';


export default {

    components: {PhiDrawer, PhiInput, PhiPersonPicker},

    data() {
        return {
            app,
            moment,
            log: app.api.collection(`/attendance/checkpoints/${this.$route.params.nodeId}/log`),
            types: app.api.collection(`/types/checkpoint`),
            tpl: {
                drawerIsOpen: false
            },
            entry: {
                person: null,
                type: null
            }
        }
    },

    methods: {

        selectPerson(person) {
            this.entry.person = person.id;
            this.saveEntry();
        },

        saveEntry() {
            app.api
                .post(`/attendance/checkpoints/${this.$route.params.nodeId}/log`, this.entry)
                .then(createdEntry => {
                    this.log.unshift(createdEntry);
                    this.tpl.drawerIsOpen = false;
                });
        }
    },

    mounted() {
        this.log.fetch();
        this.types.fetch().then(() => this.entry.type = this.types.items[0].singular);
    }
}
</script>

<style lang="scss" scoped>
footer {
    display: flex;

    .save {
        .phi-media-figure {
            color: green;
        }
    }

    .cancel {
        margin-left: 50px;
        .phi-media-figure {
            color: red;
        }
    }
}

fieldset {
    margin: 0;
    padding: 20px 16px;
    border: 0;

    .phi-input {
        display: block;
    }
}

i.phi-media-figure {
    text-align: center;
    align-self: center;
}
</style>


