<template>
	<div class="phi-person-picker">

        <button v-show="!scanner.isRunning" @click="startScan"><i class="fa fa-play"></i></button>
        <button v-show="scanner.isRunning" @click="stopScan"><i class="fa fa-pause"></i></button>

        <phi-input :label="label" v-model="query" @input="debounce()"></phi-input>
        <div class="results">
            <div v-for="person in results" class="person phi-media" @click="select(person)">
                <div class="phi-media-figure phi-avatar">
                    <img :src="person.avatar" :alt="person.firstName">
                </div>
                <h1 class="phi-media-body">{{person.firstName}} {{person.lastName}}</h1>
            </div>
        </div>
	</div>
</template>

<script>
import PhiInput from '../Input.vue';

export default {
    name: "phi-person-picker",
    components: {PhiInput},
	props: ["api", "label"],

    data() {
        return {
            query: "",
            results: [],
            scanner: {
                isRunning: false
            }
        }
    },

	methods: {
        select(person) {
            this.selected = person;
            this.$emit("select", person);
            this.reset();
        },

        find() {
            this.api
                .get("people", {q: this.query})
                .then(results => this.results = results);
        },

        debounce() {
            if (!this.query) {
                return this.reset();
            }
            clearTimeout(this.timer);
            this.timer = setTimeout(() => this.find(), 500);
        },

        reset() {
            this.query   = "";
            this.results = [];
        },

        startScan() {
            if (this.scanner.isRunning || typeof QRScanner == 'undefined') {
                return;
            }
            this.scanner.isRunning = true;

            QRScanner.scan((err, contents) => {
                if (err) {
                    return;
                }

                try {
                    this.select(JSON.parse(contents));
                } catch (e) {
                    // not valid JSON
                }

                // restart scanner
                this.scanner.isRunning = false;
                this.startScan();
            });

        },

        stopScan() {
            if (!this.scanner.isRunning) {
                return;
            }
            this.scanner.isRunning = false;

            QRScanner.cancelScan(status => {
                console.log(status);
                alert("Cancel: " + JSON.stringify(status));
            });
        }
	}
}

</script>

<style scoped lang="scss">
.phi-person-picker {
    .phi-input {
        display: block;
    }

    .results {
        max-height: 400px;
        overflow: auto;

        .person {
            .phi-avatar {
                width: 20px !important;
                height: 20px !important;
            }

            .phi-media-body {
                font-size: 1em;
                align-self: center;
            }
        }
    }
}
</style>


